/**
 * @file PlayerBaseService.ts
 * @description Service de gestion de la base du joueur
 * Implmente les mthodes pour manipuler les points de vie et les altrations de la base
 */

import { v4 as uuidv4 } from 'uuid';
import { PlayerBase, PlayerBaseConfig } from '../types/player';
import { Alteration } from '../types';
import { CardInstance } from '../types/combat';

/**
 * Implmentation de la base du joueur
 * Gre les points de vie, les dgts, la gurison et les altrations
 */
export class PlayerBaseImpl implements PlayerBase {
  id: string;
  currentHealth: number;
  maxHealth: number;
  activeAlterations: {
    alteration: Alteration;
    remainingDuration: number | null;
    stackCount: number;
    source: CardInstance | null;
  }[];
  
  /**
   * Historique des changements de sant pour le dbogage
   * Non expos dans l'interface publique
   */
  private healthChangeHistory: Array<{
    type: 'damage' | 'heal';
    amount: number;
    source?: string;
    timestamp: number;
  }> = [];
  
  /**
   * Cre une nouvelle instance de base de joueur
   * @param config - Configuration optionnelle
   */
  constructor(config?: PlayerBaseConfig) {
    this.id = uuidv4();
    this.maxHealth = config?.maxHealth || 100;
    this.currentHealth = this.maxHealth;
    this.activeAlterations = [];
  }
  
  /**
   * Applique des dgts  la base
   * @param amount - Quantit de dgts  appliquer
   * @param source - Source des dgts (carte ou autre)
   * @returns - Quantit de dgts rellement appliqus
   */
  applyDamage(amount: number, source?: string): number {
    if (amount <= 0) return 0;
    
    // Pour l'instant pas de systme de rsistance
    const actualDamage = Math.min(this.currentHealth, amount);
    this.currentHealth -= actualDamage;
    
    // Enregistre l'historique de dgts pour le dbogage
    this.healthChangeHistory.push({
      type: 'damage',
      amount: actualDamage,
      source,
      timestamp: Date.now()
    });
    
    return actualDamage;
  }
  
  /**
   * Soigne la base
   * @param amount - Quantit de points de vie  restaurer
   * @param source - Source du soin
   * @returns - Quantit de points de vie rellement restaurs
   */
  heal(amount: number, source?: string): number {
    if (amount <= 0) return 0;
    
    const missingHealth = this.maxHealth - this.currentHealth;
    const actualHeal = Math.min(missingHealth, amount);
    this.currentHealth += actualHeal;
    
    // Enregistre l'historique de soin pour le dbogage
    this.healthChangeHistory.push({
      type: 'heal',
      amount: actualHeal,
      source,
      timestamp: Date.now()
    });
    
    return actualHeal;
  }
  
  /**
   * Ajoute une altration  la base
   * @param alteration - L'altration  appliquer
   * @param source - Source de l'altration
   */
  addAlteration(alteration: Alteration, source: CardInstance | null): void {
    // Si l'altration n'est pas empilable, vrifier si elle existe dj
    if (!alteration.stackable) {
      const existingIndex = this.activeAlterations.findIndex(
        a => a.alteration.id === alteration.id
      );
      
      // Si elle existe, rinitialiser sa dure
      if (existingIndex >= 0) {
        this.activeAlterations[existingIndex].remainingDuration = 
          alteration.duration || null;
        return;
      }
    } else {
      // Si l'altration est empilable, vrifier si elle existe dj
      const existingIndex = this.activeAlterations.findIndex(
        a => a.alteration.id === alteration.id
      );
      
      // Si elle existe, augmenter son compteur d'empilement
      if (existingIndex >= 0) {
        this.activeAlterations[existingIndex].stackCount += 1;
        // Rinitialise la dure si elle est temporaire
        if (alteration.duration) {
          this.activeAlterations[existingIndex].remainingDuration = 
            alteration.duration;
        }
        return;
      }
    }
    
    // Ajouter une nouvelle altration
    this.activeAlterations.push({
      alteration,
      remainingDuration: alteration.duration || null,
      stackCount: 1,
      source
    });
  }
  
  /**
   * Supprime une altration de la base
   * @param alterationId - ID de l'altration  supprimer
   */
  removeAlteration(alterationId: number): void {
    const index = this.activeAlterations.findIndex(
      a => a.alteration.id === alterationId
    );
    
    if (index >= 0) {
      this.activeAlterations.splice(index, 1);
    }
  }
  
  /**
   * Vrifie si la base a une altration spcifique
   * @param alterationId - ID de l'altration  vrifier
   * @returns - True si l'altration est prsente
   */
  hasAlteration(alterationId: number): boolean {
    return this.activeAlterations.some(a => a.alteration.id === alterationId);
  }
  
  /**
   * Applique les effets des altrations actives
   */
  applyAlterationEffects(): void {
    // Implmentation future pour appliquer les effets spcifiques des altrations
    // Pour l'instant, nous nous contentons de grer leur dure
  }
  
  /**
   * Restaure la base pour le tour suivant
   * Rduit la dure des altrations temporaires, etc.
   */
  resetForNextTurn(): void {
    // Met  jour les dures des altrations
    for (let i = this.activeAlterations.length - 1; i >= 0; i--) {
      const alteration = this.activeAlterations[i];
      
      // Si l'altration a une dure
      if (alteration.remainingDuration !== null) {
        alteration.remainingDuration--;
        
        // Supprime l'altration si sa dure est coule
        if (alteration.remainingDuration <= 0) {
          this.activeAlterations.splice(i, 1);
        }
      }
    }
  }
  
  /**
   * Vrifie si la base est dtruite (PV <= 0)
   * @returns - True si la base est dtruite
   */
  isDestroyed(): boolean {
    return this.currentHealth <= 0;
  }
}

/**
 * Cre une nouvelle instance de base de joueur
 * @param config - Configuration optionnelle
 * @returns - Une nouvelle instance de PlayerBase
 */
export const createPlayerBase = (config?: PlayerBaseConfig): PlayerBase => {
  return new PlayerBaseImpl(config);
}; 